import axios from "axios";
import { MessageBox, Message } from "element-ui";
// import store from '@/store'
import { getToken } from "@/utils/auth";

// create an axios instance
const service = axios.create({
  // baseURL: process.env.VUE_APP_BASE_API, // api  base_url
  baseURL: "/mock", // api  base_url
  withCredentials: true, //  cookies
  timeout: 5000 // request timeout
});

// request interceptor
service.interceptors.request.use(
  config => {
    // Do something before request is sent
    // if (store.getters.token) {
    //   // token-- ['X-Token']key 
    //   config.headers['X-Token'] = getToken()
    // }
    return config;
  },
  error => {
    // Do something with request error
    // console.log(error); // for debug
    return Promise.reject(error);
  }
);

// response interceptor
service.interceptors.response.use(
  /**
   * If you want to get information such as headers or status
   * Please return  response => response
   */
  /**
   * responsecode
   * code
   *  XMLHttpRequest  error
   * 
   */
  response => {
    return response;
  },
  err => {
    // console.log('err' + error) // for debug
    // Message({
    //   message: error.message,
    //   type: 'error',
    //   duration: 5 * 1000
    // })

    if (err && err.response) {
      var responseMessage = err.response.data.message;
      switch (err.response.status) {
        case 400:
          err.response.message = responseMessage || "";
          Message({
            message: "",
            type: "warning"
          });
          break;

        case 401:
          err.response.message = responseMessage || "";
          Message({
            message: "",
            type: "warning"
          });
          break;

        case 403:
          err.response.message = responseMessage || "";
          Message({
            message: "",
            type: "warning"
          });
          break;

        case 404:
          err.response.message =
            responseMessage || `: ${err.response.config.url}`;
          Message({
            message: ": ${err.response.config.url}",
            type: "warning"
          });
          break;

        case 405:
          err.response.message = responseMessage || "";
          Message({
            message: "",
            type: "warning"
          });
          break;

        case 408:
          err.response.message = responseMessage || "";
          Message({
            message: "",
            type: "warning"
          });
          break;

        case 500:
          err.response.message = responseMessage || "";

          if (
            err.response.data["exception"] ===
            "io.jsonwebtoken.ExpiredJwtException"
          ) {
            var msg = "Token ,";
            if (confirm(msg) === true) {
              router.push("/login");
              location.reload();
            } else {
            }
          } else {
          }
          break;

        case 501:
          err.response.message = responseMessage || "";
          Message({
            message: "",
            type: "warning"
          });
          break;

        case 502:
          err.response.message = responseMessage || "";
          Message({
            message: "",
            type: "warning"
          });
          break;

        case 503:
          err.response.message = responseMessage || "";
          Message({
            message: "",
            type: "warning"
          });
          break;

        case 504:
          err.response.message = responseMessage || "";
          Message({
            message: "",
            type: "warning"
          });
          break;

        case 505:
          err.response.message = responseMessage || "HTTP";
          Message({
            message: "HTTP",
            type: "warning"
          });
          break;

        default:
          err.response.message = responseMessage;
      }
    }
    return Promise.reject(err);
  }
);

export default service;



